import { Typography } from "@mui/material";
import { Container } from "@mui/system";
import React from "react";
import './Home.css';


const Home=()=>{
    return(
        <Container sx={{mt:5}}>
            <Typography variant="h2" component="h1" sx={{textAlign:"center"}}> Bienvenidos a CityMapper</Typography>
            <Typography sx={{textAlign:"center", mt:7}}>Bienvenidos a la app de turismo numero 1! En la seccin "clima" podrs consultar el clima
            de cualquier ciudad que desees. En el apartado de "ciudades" podrs inspeccionar las principales ciudades de Amrica con sus reseas. Adntrate en el mundo del turismo</Typography>
        </Container>
        
    )  
}

export default Home;